import { FC } from "react";
import { ButtonProps } from "./types-dashed-button";
import { Wrapper, Button as AsButton ,Img} from "./styles-dashed-button";

const DashedButton: FC<ButtonProps> = ({ value, type, onClick, icon }) => {
  return (
    <Wrapper>
      <AsButton type={type} onClick={onClick}>
        {icon && <Img src={icon} alt="icon-button" />}
        {value}
      </AsButton>
    </Wrapper>
  );
};

export default DashedButton;
