import { FC, useState } from "react";
import { AddCard, Icon, Label, Title, Wrapper } from "./styles-add-company";
import { AddCompanyProps } from "./types-add-company";
import add from "@assets/images/dashboard/add.svg";
import ModalAddCompany from "./modal-add-company/modal-add-company";
const AddCompany: FC<AddCompanyProps> = () => {
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    setOpen(true);
  };
  return (
    <Wrapper>
      <ModalAddCompany open={open} handleClose={handleClose} />
      <Title>
        Tezkor dasturiga xush kelibsiz. Ishni boshlash uchun kompaniyangizni
        qo’shing!
      </Title>
      <AddCard onClick={handleOpen}>
        <Icon src={add} />
        <Label>Kompaniya qo’shish</Label>
      </AddCard>
    </Wrapper>
  );
};

export default AddCompany;
