import { api } from "@src/services/apis/client"
import { useQuery, UseQueryOptions } from "react-query"
import { ICompany } from "@src/types";



const fetchData = async () =>{
    try{
        let res = await api.get('/company/companies/')
        return res.data.data
    }catch(err:any){
        throw new Error(err);
    }
}
type IOption = Omit<
//@ts-ignore
  UseQueryOptions<TQueryFnData, TError, TData, TQueryKey>,
  'queryKey' | 'queryFn'
>;
export const useCompanies = (params?: IOption) =>{
    return useQuery<any,Error, ICompany[]>("companies", fetchData,{
        ...params,
    })
}