import styled from 'styled-components';

export const Wrapper = styled.div`
  width: 100%;
  padding: 90px 0;
  background-color: #ffffff;
  text-align: center;
`;

export const Title = styled.h1`
  font-weight: 600;
  font-size: 32px;
  line-height: 46px;
  text-align: center;
  color: #000000;
  width: 60%;
  margin: 0 auto;
`;

export const AddCard = styled.div`
  width: 210px;
  padding: 29px;
  margin: 30px auto;
  text-align: center;
  cursor: pointer;
`;

export const Icon = styled.img``;

export const Label = styled.p`
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #0496ff;
  margin-top: 41px;
`;
