import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
`;

export const Button = styled.button`
  cursor: pointer;
  width: 100%;
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  max-height: 60px;
  height: 100%;
  font-size: 16px;
  font-weight: 400;
  color: #666;
  border: 2px dashed #0496ff;
  padding: 21px 0;
`;
export const Img = styled.img`
  margin-right: 18px;
`;
