import { FC, useState } from 'react';
import AddCompany from './add-company/add-company';
import {
  Wrapper,
  Title,
  CompanyCard,
  Img,
  Name,
  Icon,
  Card,
} from './styles-companies';
import { CompaniesProps } from './types-companies';
import right_transition from '@assets/images/dashboard/right_transition.svg';
import { GeneralContainer } from '@components/common';
import { useCompanies } from '@src/hooks/useCompanies';

const Companies: FC<CompaniesProps> = () => {
  const [ active, setActive] = useState(0)
  const { isLoading, data, isError } = useCompanies({
    onError: (err) => {
      console.log('companies error: ', err);
    },
  });
  console.log('companies data: ', data);
  const handleClose = () =>{}
  return (
    <GeneralContainer>
      <Wrapper>
        <Title>Kompaniyangizni tanlang</Title>
        {data?.map((item: any, index: number) => (
          <CompanyCard key={index}>
            {item.image ? <Img src="https://picsum.photos/700" alt="company img" /> : <p>Company image not</p>}
            <Card>
              <Name>{item?.title}</Name>
              {/* <IconCard> */}
              <Icon src={right_transition} />
              {/* </IconCard> */}
            </Card>
          </CompanyCard>
        ))}
        <AddCompany />
      </Wrapper>
    </GeneralContainer>
  );
};

export default Companies;
