import { FC, useState } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import {useDispatch} from 'react-redux'
import { yupResolver } from "@hookform/resolvers/yup";
import { Button, Input, Selector, UniversalModal } from "@components/common";
import { Title, Wrapper } from "./styles-modal-add-company";
import icon from "@assets/images/dashboard/dashboard-modal/upload-bracket.svg";
import {
  IAddCompanyForm,
  ModalAddCompanyProps,
  schema,
} from "./types-modal-add-company";
import DashedButton from "@components/common/dashed-button/dashed-button";
const ModalAddCompany: FC<ModalAddCompanyProps> = ({ open, handleClose }) => {
  const [companyName, setCompanyName] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [selectedValue, setSelectedValue] = useState<string>("");
  const [selectedValue2, setSelectedValue2] = useState<string>("");
  const dispatch = useDispatch()
  const {
    handleSubmit,
    control,
    formState: { errors },
  } = useForm<IAddCompanyForm>({
    defaultValues: {
      companyName: "",
      companyRoute: "",
      companySubRoute: "",
      email: "",
    },
    resolver: yupResolver(schema),
  });
  const onSubmit:SubmitHandler<IAddCompanyForm>=(data)=>{
    // dispatch()
  }
  const options = ["salom", "salw"];
  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper onSubmit={handleSubmit(onSubmit)}>
        <Title>Kompaniya qo’shish</Title>
        <Input
          title="Kompaniya nomini kiriting"
          name="company"
          placeholder="Kompaniya nomi"
          onChange={setCompanyName}
          control={control}
        />
        <Selector
          placeholder="Ta’lim yo’nalishi"
          selectedValue={(value) => setSelectedValue(value)}
          options={options}
          title="Kompaniya yo’nalish"
        />
        <Selector
          placeholder="Maktabgacha ta’lim muassasi"
          selectedValue={(value) => setSelectedValue2(value)}
          options={options}
        />
        <Input
          title="Kompaniya e-mail manzilini kiriting"
          name="email"
          placeholder="E-mail"
          onChange={setEmail}
          control={control}
        />
        <DashedButton type="button" value="Logo yuklash" icon={icon} />
        <Button type="button" value="Saqlash" />
      </Wrapper>
    </UniversalModal>
  );
};

export default ModalAddCompany;
