import * as yup from "yup";
export interface ModalAddCompanyProps {
  open: boolean;
  handleClose: () => void;
}
export type IAddCompanyForm = {
  companyName: string;
  companyRoute: string;
  companySubRoute: string;
  email: string;
};
export const schema = yup.object().shape({
  companyName: yup.string().required("Kompaniya nomi kiritilishi shart !!"),
  companyRoute: yup
    .string()
    .required("Kompaniya yo'nalishini tanlash shart !!"),
  companySubRoute: yup
    .string()
    .required("Kompaniya sub yo'nalishini tanlash shart !!"),
  email: yup
    .string()
    .email("Iltimos emailni to'g'ri kiriting")
    .required("Email bo'lishi shart !!"),
});
